<template>
  <Breadcrumb
    class="mt-2 mb-8"
    pageTitle="Edit Notification"
    :backTo="{
      name: 'settingEmailTemplate',
      query: {
        filter: 1
      }
    }"
    :items="breadcrumb">
    <template #right>
      <Button
        :loading="loading.update"
        color="teal"
        @click="updateTemplate">
        Save Changes
      </Button>
    </template>
  </Breadcrumb>
  <Card class="mb-6">
    <div class="grid grid-cols-2">
      <div class="col-span-2 lg:col-span-1">
        <div class="mb-4">
          <label class="mb-2">Title</label>
          <p>{{ data.email_template.name }}</p>
        </div>
        <Input
          :value="dataCompany.email_sender"
          class="mb-4"
          label="From"
          block
          disabled
          :loading="loadingCompany.show"
        />
        <template v-if="isShowTimeframe">
          <!-- <Counter
            v-model="data.email_template.delay_per_days"
            class="mb-4"
            id="delay"
            min="0"
          >
            <template #label>
              When should we begin sending emails after a customer has abandoned
              their trade-in? test
              <span class="font-normal text-grey-03">(days)</span>
            </template>
          </Counter>
          <Counter
            v-model="data.email_template.sending_times"
            class="mb-4"
            id="sending-times"
            min="0"
            max="3"
          >
            <template #label>
              How many times would you like this email to trigger in total?
              <span class="font-normal text-grey-03">(maximum 3)</span>
            </template>
          </Counter> -->
          <EmailTemplateTimeDelay
            :sendingTimes="data.email_template.sending_times"
            :sendingTimesJson="data.email_template.sending_times_json"
            @update:sendingTimes="handleUpdateSendingTimes"
            @update:sendingTimesJson="handleUpdateSendingTimesJson"
          />
        </template>
        <div class="my-4">
          <label>BCC</label>
          <MultipleEmailInput
            @email-change="handleUpdateBccEmail"
            :suggestion="false"
            :emails="data.email_template.bcc"
          />
        </div>
        <Input
          class="mb-4"
          v-model="data.email_template.subject"
          label="Subject"
          block
        />
      </div>
      <div v-loading="loading.show" class="col-span-2">
        <TextEditor
          v-model="data.email_template.body"
          ref="refTextEditor"
          :options="ckeditor"
          with-image-upload
        >
          <template #toolbar>
            <Button
              class="ml-2"
              @click="handlePreviewEmailTemplate"
              :loading="loading.preview_email"
              color="teal"
              dense
            >
              <i class="material-icons mr-1">visibility</i>
              Preview Email
            </Button>
            <Button
              @click="openModal('reset_template')"
              :loading="loading.reset_template"
              class="ml-auto"
              color="orange"
              view="smooth"
              dense
              ><i class="material-icons">restart_alt</i> Reset to Default
            </Button>
          </template>
        </TextEditor>
        <Table class="mt-6" v-if="shortCodes.length > 0" :data="shortCodes">
          <TableColumn label="Code">
            <template #default="scope">
              <div
                :class="[
                  'short-code',
                  scope.row.is_disabled ? 'short-code--disabled' : ''
                ]"
              >
                <span class="mr-2">{{ scope.row.code }}</span>
                <i
                  v-if="scope.row.is_disabled ? scope.row.disabled : true"
                  class="material-symbols-outlined text-lg text-teal-04"
                  style="font-variation-settings: 'FILL' 0"
                  @click="copyText(scope.row.code)"
                >
                  content_copy
                </i>
                <Tooltip v-if="scope.row.tooltip" placement="top">
                  <template #content>
                    <div class="w-96">
                      <span v-html="scope.row.tooltip"></span>
                    </div>
                  </template>
                  <template #default>
                    <i class="material-icons text-lg text-teal-04">info</i>
                  </template>
                </Tooltip>
              </div>
            </template>
          </TableColumn>
          <TableColumn label="Description">
            <template #default="scope">
              <div
                :class="[
                  'inline-block',
                  'short-code',
                  scope.row.is_disabled ? 'short-code--disabled' : ''
                ]"
              >
                {{ scope.row.description }}
              </div>
            </template>
          </TableColumn>
        </Table>
      </div>
    </div>
  </Card>
  <Modal v-model="modal.reset_template" hide-footer hide-header size="xs">
    <template #default>
      <div class="py-10">
        <img
          class="mb-10 mx-auto"
          src="@/assets/icon/warning.svg"
          alt="Warning icon"
        />
        <div class="mt-8 mb-10">
          <h5 class="mb-4 text-center">Reset Email Template</h5>
          <p class="text-center mb-6">
            Are you sure you want to reset to the default email template?
          </p>
        </div>
        <div class="text-center">
          <Button
            @click="closeModal('reset_template')"
            view="outlined"
            color="teal"
            class="bg-white"
            :disabled="loading.reset_template"
            >Cancel
          </Button>
          <Button
            @click="handleResetTemplate"
            view="smooth"
            color="orange"
            class="ml-3"
            :loading="loading.reset_template"
            >Yes, reset
          </Button>
        </div>
      </div>
    </template>
  </Modal>
  <PreviewEmailTemplate
    v-model:show="isPreviewEmailTemplate"
    :body="previewedEmailTemplate"
    render-type="direct"
  />
</template>

<script>
import { computed, defineComponent, reactive, ref } from 'vue'
import { useCompanyProfile, useEmailTemplate } from 'composable/setting'
import { useUser } from 'composable/user'
import { useRoute } from 'vue-router'
import { ElMessage } from 'element-plus'

import Card from 'bbUi/components/card/card.vue'
import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import Alert from 'bbUi/components/alert/alert.vue'
import Pagination from 'components/Pagination.vue'
import Breadcrumb from 'components/Breadcrumb.vue'
import MultipleEmailInput from 'components/MultipleEmailInput.vue'
import EmailTemplateTimeDelay from 'components/EmailTemplate/TimeDelay/index.vue'

import PreviewEmailTemplate from 'components/EmailTemplate/PreviewEmailTemplate.vue'
import TextEditor from 'components/TextEditor.vue'
import { Editor as ClassicEditor } from '@erdeprsty/ckeditor5-custom-build'

export default defineComponent({
  components: {
    Card,
    Input,
    Button,
    Modal,
    Alert,
    Breadcrumb,
    Pagination,
    PreviewEmailTemplate,
    TextEditor,
    MultipleEmailInput,
    EmailTemplateTimeDelay
  },
  async setup() {
    const {
      state,
      data,
      loading,
      modal,
      openModal,
      closeModal,
      previewedEmailTemplate,
      isPreviewEmailTemplate,
      actionShow: actionShowTemplate,
      actionUpdate,
      actionPreviewEmailTemplate,
      actionPreviewEmailTemplateOnly,
      actionResetTemplate
    } = useEmailTemplate()
    const {
      data: dataCompany,
      actionShow: actionShowCompany,
      loading: loadingCompany
    } = useCompanyProfile()
    const route = useRoute()
    const { me } = useUser()

    const refTextEditor = ref(null)
    const isShowTimeframe = computed(() => {
      const chaserEmailTemplateKey = [
        'in-store-chaser-email-10-day',
        'chaser-email-10-day',
        'cart-recovery',
        'in-store-cart-recovery',
        'easy-offer-chaser-email-10-day',
        'easy-offer-cart-recovery'
      ]
      const isChaserEmail = chaserEmailTemplateKey.includes(
        data.email_template.key
      )
      return isChaserEmail
    })
    const shortCodes = computed(() => {
      return (data?.email_template?.sort_code || []).map((code) => ({
        ...code,
        description: code.description.replace(/\\/g, ''),
        tooltip: code.tooltip ? code.tooltip.replace(/\\n/g, '<br/>') : null
      }))
    })

    const breadcrumb = [
      {
        text: 'Settings',
        to: {
          name: 'setting'
        }
      },
      {
        text: 'Edit Notification',
        to: {
          name: 'settingEmailTemplate',
          query: {
            filter: 1
          }
        }
      },
      {
        text: 'Edit'
      }
    ]
    const ckeditor = reactive({
      editor: ClassicEditor,
      config: {
        htmlSupport: {
          allow: [
            {
              styles: true,
              classes: true
            }
          ]
        },
        link: {
          decorators: {
            test: {
              mode: 'automatic',
              callback: (url) => url === '{:verify-link}',
              classes: 'btn btn-blue btn-filled my-4'
            }
          }
        },
        simpleUpload: {
          uploadUrl: `${
            import.meta.env.VITE_BASE_URL_API
          }/v1/pos/settings/email-template/upload-image/${
            route.params.templateId
          }?buyback_company_token=${me.value.buyback_company_token}`
        }
      }
    })

    const copyText = (text) => {
      navigator.clipboard.writeText(text).then(() => {
        ElMessage({
          type: 'success',
          message: 'Text copied to clipboard'
        })
      })
    }

    const handleUpdateSendingTimes = (value) => {
      data.email_template.sending_times = value
    }

    const handleUpdateSendingTimesJson = (value) => {
      data.email_template.sending_times_json = value
    }
    const updateTemplate = async () => {
      loading.update = true
      const isReadyEditor = refTextEditor.value.isEditorReady
      if (!isReadyEditor) return
      const isSaved = await refTextEditor.value.forceSave()
      if (!isSaved) return

      await actionUpdate(
        { id: route.params.templateId, type: route.query.type },
        () => {
          ElMessage({
            type: 'success',
            message: 'Email template change'
          })
        }
      )
      loading.update = false
    }
    const handlePreviewEmailTemplate = async () => {
      await actionPreviewEmailTemplateOnly(route.params.templateId)
    }
    const handleResetTemplate = async () => {
      await actionResetTemplate(async () => {
        closeModal('reset_template')
        await actionShowTemplate({
          id: route.params.templateId,
          type: route.query.type
        })
      })
    }
    const handleUpdateBccEmail = (emails=[]) => {
      data.email_template.bcc = [...emails]
    }

    await actionShowTemplate({
      id: route.params.templateId,
      type: route.query.type
    })
    await actionShowCompany()

    return {
      refTextEditor,
      breadcrumb,
      ckeditor,
      state,
      dataCompany,
      data,
      loading,
      modal,
      loadingCompany,
      previewedEmailTemplate,
      isPreviewEmailTemplate,
      isShowTimeframe,
      shortCodes,
      openModal,
      closeModal,
      copyText,
      updateTemplate,
      handlePreviewEmailTemplate,
      handleResetTemplate,
      handleUpdateBccEmail,
      handleUpdateSendingTimes,
      handleUpdateSendingTimesJson
    }
  }
})
</script>

<style lang="scss">
.ck-reset_all {
  .btn {
    all: initial;
  }
}

.short-code {
  @apply flex gap-2;
  &--disabled {
    @apply text-grey-03;
  }
}
</style>
